
import XHeader from 'vux/src/components/x-header/index.vue'

import { getQryInfo, getBlock } from 'api'
export default {
data () {
return {
info: {},
cmd: this.$route.params.cmd,
args: this.$route.params.args,
max: ''
}
},
created () {
this.getQryInfo()
this.getMaxBlock() 
},
beforeRouteUpdate (to, from, next) {
this.cmd = to.params.cmd
this.args = to.params.args
this.getQryInfo()
next()
},
computed: {
minerList () {
return this.info.MinerList.split(',') || []
},
minDisabled () {
return !this.info.PreviousHash
},
maxDisabled () {
return this.info.Number === this.max
}
},
methods: {
getQryInfo () {
this.$vux.loading.show()
getQryInfo({cmd: this.cmd, args: this.args}).then(res => {
this.info = res.data
})
.finally(() => {
this.$vux.loading.hide()
})
},
getInfo (val) {
let prev = Number(this.info.Number) - 1
let next = Number(this.info.Number) + 1
let args
val === 'prev' ? args = prev : args = next
this.$router.push({name: 'BlockDetail', params: {args}})
},
getMaxBlock () {
getBlock({method: 'max'}).then(res => {
this.max = res.data
})
}
},
components: {
XHeader
}
}



